@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,400&display=swap);
html{font-family:'Open Sans', sans-serif;font-size:20px}body{background:#fdfdfd;color:#3c3c3c}a{font-weight:400;text-decoration:underline}video,img{border:1px solid #8695aa;max-width:100%}figcaption{background:#8695aa;color:white;font-size:0.6rem;text-transform:uppercase}.gallery{background:#181e2d;color:#fdfdfd}.gallery__header{color:#20fc8f;font-weight:lighter;letter-spacing:0.05rem}.gist-container{max-width:100%}.gist-container .gist-data{max-height:40vh}h1,h2,h3,h4,h5,h6{color:#000;word-break:break-word}h1{font-size:3.5rem;font-weight:lighter}h2{font-size:3rem;font-weight:lighter}.link{background:none;border:none;color:#20fc8f;word-break:break-all;text-decoration:underline}.link:link,.link:visited{color:#20fc8f}.link:hover{color:#85fdc2}.link:active{color:#20fc8f}.mobile-name{line-height:0.8;margin-bottom:2.5rem}.mobile-name__given{margin-right:1.5rem}.mobile-name__surname{margin-left:1.5rem}.portfolio-items__item{background:#a74eff;border:none;font-size:0.8rem;font-weight:bold}.portfolio-items__item.btn-primary{background:#a74eff}.portfolio-items__item.btn-primary:hover{background:#c081ff}.portfolio-item__header,.portfolio-item__footer{background:#1a2234;color:#fff}.portfolio-item__urls{background:#000}.tag{background:#a74eff;color:#fff;font-size:0.8rem;white-space:nowrap}

